import axios from 'axios';
import qs from 'qs';
import {
  Message,
  Loading
} from 'element-ui'
import router from '@/router';
import { delCookie } from '@/utils/base.js';

//axios.defaults.timeout = 5000;                        //
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; //
//axios.defaults.baseURL = 'http://www.jjj-shop.com/';   //
axios.defaults.baseURL = '/index.php'; //
axios.defaults.withCredentials = true;
axios.defaults.responseType = 'json';

//POST()
axios.interceptors.request.use((config) => {
  //
  if (config.method === 'post') {
    config.data = qs.stringify(config.data);
  }
  return config;
}, (error) => {
  console.log('')
  return Promise.reject(error);
});

//()
axios.interceptors.response.use((res) => {
  //
  if (res.data.code !== 1) {
    console.log('')
    if(res.data.code===0){
      Message({
        showClose: true,
        message: res.data.msg,
        type: "error"
      });
      return Promise.reject(res.data);
    }else{
      delCookie('isLogin');
      router.push({
      	path: '/login',
      	query: {
      	}
      })
    }
  }else{
    return res.data;
  }
}, (error) => {
  Message({
    showClose: true,
    message: '~',
    type: "error"
  });
  return Promise.reject(error);
});

/**
 * Promise(post)
 * errorback
 */
export function _post(url, params, errorback) {
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      .then(response => {
        resolve(response);
      })
      .catch((error) => {
        errorback && reject(error);
      })
  })
}

/**
 * Promise(get)
 * errorback
 */
export function _get(url, param, errorback) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
        params: param
      })
      .then(response => {
        resolve(response)
      })
      .catch((error) => {
        errorback && reject(error);
      })
  })
}
export default {
  _post,
  _get,
}



